body {
  background-color: #222;
}

.banner-bg {
  --s: 150px; /* control the size */
  --c1: #1d1d1d;
  --c2: #4e4f51;
  --c3: #3c3c3c;
  background:
    repeating-conic-gradient(from 30deg,#0000 0 120deg,var(--c3) 0 180deg) 
     calc(.5*var(--s)) calc(.5*var(--s)*0.577),
    repeating-conic-gradient(from 30deg,var(--c1) 0 60deg,var(--c2) 0 120deg,var(--c3) 0 180deg);
  background-size: var(--s) calc(var(--s)*0.577);
  border: 10px solid darkslategray;
}

.app-container-start {
  background-color: #5a615b;
  min-height: 30vh;
}

.text-inlay {
  -webkit-text-fill-color: transparent;
  -webkit-background-clip: text;
  background: linear-gradient(
  to bottom,
  #97aa89,
  #97aa89 50%,
  #627765 50%,
  #627765
  );
  background-size: 20% 15px;
  background-clip: text;
  display: block;
  text-shadow: 2px -2px 10px white;
}

.cantata-one-regular {
  font-family: "Cantata One", serif;
  font-weight: 400;
  font-size: 28px;
  font-style: normal;
}

.step-1 {
  font-family: 'PT Serif', serif, sans-serif;
  text-shadow: 0px 5px 5px #4b4b4b;
}

.learn-more {
  font-family: 'Lora', sans-serif;
  color: #2196f3;
}

.step-2 {
  font-family:'Lucida Sans', sans-serif, Verdana;
  font-size: 14px;
}

.media-box {
  background: #464646;
  flex: 1 100%;
  position: relative;
  background: linear-gradient(to bottom, #85a19f, #9fb8cf);
  padding: 10px;
  margin-top: 20px;
  border-top: 10px solid gray;
}

.flex-mobi {
  display: flex;
  flex-direction: column;
}

.modal-style {
  display:flex;
  height: 450px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #3b3b3bd0;
  width: 700px;
  border: 3px solid #000000ad;
  flex-direction: column;
  padding: 20px;
  z-index: 5;
}

.modal-style-2 {
  background-color: #4d4d4deb;
}

.mytbl{
  max-height: calc(100vh - 120px);
  overflow-y: scroll;
}

.app-container {
  background-color: #5a615b;
  min-height: 50vh;
}

.posts-container {

}

.add-post {
  display: flex;
  padding: 10px;
}

form {
  padding: 3rem;
  background-image: linear-gradient(120deg,#c4c4c4 15%, #bebebe 30%, #e6e6e6 55%, #a8a8a8 85%); 
  border: 7px solid #c2c2c2;
  display: grid;
  gap: 2rem;
  background-position: 0% 50%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

form label {
  font-family: 'Lora', Helvetica, sans-serif;
  font-size: 17px;
}

form h3 {
  margin: 0px;
  text-align: center;
  font-family: Helvetica, sans-serif;
  font-size: 20px;
}

#post-title {
  width: 100%;
  height: 3rem;
  font-size: 15px;
  font-family: Helvetica, sans-serif;
}

#post-title-h3 {
  width: 100%;
  font-size: 15px;
  font-family: Helvetica, sans-serif;
}

#post-content {
  width: 100%;
  height: 18rem;
  resize: none;
  font-size: 15px;
  font-family: Helvetica, sans-serif;
  padding: 15px;
}

textarea, input {
  border-radius: 10px;
  border: 2px solid #8f8f8f;
}

.posts-item {
  position: relative;
  display: flex;
  flex-direction: row;
  border: 4px solid #8d8d8d;
  border-radius: 5px;
  background-color: #979797;
  padding-top: 12px;
  box-shadow: -1px 6px 8px #535353;
  margin-top: -3px;
  padding-bottom: 12px;
  padding-left: 10px;
  font-family: 'Lora', sans-serif, Helvetica;
  font-size: 18px;
  max-width: 55ch;
  max-height: 4ch;
  overflow: hidden; 
  text-overflow: ellipsis;
  white-space: normal;
  display: block;
  background-image: linear-gradient(180deg, #a0a0a0 0%, #787a85 99%);
  background-position: 0% 50%;
  cursor: pointer;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
    background-size: 100% 100%;
  }
}

@keyframes gradient-2 {
  0% {
    background-position: 0% 50%;
    background-size: 200% 200%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
    background-size: 125% 100%;
  }
}

.post-title {
  background-size: 125% 100%;
  background-position: 0% 50%;
  padding-top: 20px;
}

.posts-item:hover {
  background-size: 150% 150%;
	animation: gradient 4s ease;
}

.post-title-focus {
  background-size: 125% 100%;
	animation: gradient-2 4s ease;
  padding-top: 20px;
  background-position: 0% 50%;
}

.post-arrow-right {
  position: absolute;
  top: 0px;
  right: -10px;
}

.current-post-title {
  background-color: #b4b4b4;
  padding: 15px;
  border-radius: 10px;
}

.current-post-content {
  padding: 15px;
  background-image: linear-gradient(180deg, #818181 10%, #727272 90%);
  border: 4px solid grey;
  border-radius: 10px;
}

.card {
  background-color: whitesmoke !important;
  margin: 5px;
}

.alternate {
  background-color: #c4c4c4 !important;
  margin: 10px;
}

.last-font {
  font-family: Verdana, sans-serif;
  font-size: 13px;
  padding: 10px;
  margin-bottom: -10px;
}

.choose-plus {
  font-family:'Lucida Sans', Geneva, sans-serif;
  font-size: 13px;
  padding: 10px;
}

.title-font {
  font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
}

.artist-font {
  font-family: Geneva, sans-serif;
  color: #242a61;
  font-weight: 550;
}

.song-tile {
  padding: 5px;
  flex: 0 100%;
}

.song-tile-font {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.song-tile-artist {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.book-tile {
  padding: 5px;
  flex: 0 100%;
}

.book-tile-font {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.book-tile-artist {
  margin: -10px;
  font-family: 'Lucida Sans Regular', sans-serif, Verdana
}

.audio-file {
  border-bottom: 3px solid #183492;
}

.menu-book {
  border-bottom: 3px solid #183492;
}

@media screen and (max-width: 980px) {
}

@media screen and (max-width: 768px) {
  .flex-mobi {
    flex-direction: column;
  }
  .modal-style {
    width: 600px;
  }
  .media-box {
    margin-top: 0px;
    border-top: 0px;
  }
  .posts-item {
    max-width: 37ch;
    max-height: 4ch;
  }
}

@media screen and (max-width: 575px) {
  .modal-style {
    width: 300px;
  }
  .song-tile-font {
    font-family: monospace, sans-serif, Verdana;
  }
  .song-tile-artist {
    font-family: monospace, sans-serif, Verdana;
  }
  .book-tile-font {
    font-family: monospace, sans-serif, Verdana;
  }
  .book-tile-artist {
    font-family: monospace, sans-serif, Verdana;
  }
}